<template>
  <div id="app" class="bg">
    <div class="mask" v-show="showRules" @click="toggleShowRules" :style="{height: getHeight()}"></div>
    <div class="rules bg" v-show="showRules">
      <div class="rules-container">
      <p>{{ $t('index.rules[0]') }}</p>
      <p>{{ $t('index.rules[1]') }}</p>
      <p>{{ $t('index.rules[2]') }}</p>
      <p>{{ $t('index.rules[3]') }}</p>
      <p>{{ $t('index.rules[4]') }}</p>
      <p>{{ $t('index.rules[5]') }}</p>
      </div>
    </div>
    <div class="banner bg"></div>
    <div class="desc">
      <span @click="toggleShowRules">{{ $t('index.slogonDetail[0]') }}</span>
      <span><a target="_blank" href="baidu.com">{{ $t('index.slogonDetail[1]') }}</a></span>
      <span><a target="_blank" href="mi.com">{{ $t('index.slogonDetail[2]') }}</a></span>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import Web3 from "web3"
import store from './store'
import { mapState, mapGetters, mapActions } from 'vuex'
import TokenJson from '../../contract/build/contracts/SFtoken.json'
import SuperFair from '../../contract/build/contracts/SuperFair.json'

export default {
  name: 'app',
  data () {
    return {
      account: ethereum && ethereum.selectedAddress || '登录钱包',
      currentLanguage: localStorage.getItem('currentLocale') && localStorage.getItem('currentLocale') == 'en' ? 'English' : '中文简体',
      currentNetworkVersion:
        web3.currentProvider.connection && web3.currentProvider.connection.networkVersion || '5777',
      showRules: false
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.userInfo,
      inviteUrl: state => `${window.location.origin}/#/?referrer=${state.userInfo && state.userInfo[0]}`
    }),
  },
  components: {
  },
  methods: {
    toggleShowRules() {
      this.showRules = !this.showRules
    },
    getHeight() {
      return document.querySelector('#app').offsetHeight + 'px'
    },
    handleCommand(command) {
      this._i18n.locale = command
      if (command === 'cn') {
        this.currentLanguage = '中文简体'
      } else if (command === 'en') {
        this.currentLanguage = 'English'
      }
      localStorage.setItem('currentLocale', command)
    },
    async initTokenContract() {
      const address = TokenJson["networks"][this.currentNetworkVersion].address
      const contract = await new web3.eth.Contract(
        TokenJson.abi,
        address
      )
      return contract
    },
    async initContract() {
      const address = SuperFair["networks"][this.currentNetworkVersion].address
      const contract = await new web3.eth.Contract(
        SuperFair.abi,
        address
      )
      return contract
    },
    async unlockMetaMask() {
      const self = this
      if (window.web3 && window.web3.currentProvider.isTrust) {
        window.web3 = new Web3(web3.currentProvider)
        const account = web3.eth.accounts[0]
        this.$alert(`trust`)
        self.account = account
      } else if (window.ethereum) {
        window.web3 = new Web3(ethereum)
        try {
          await ethereum.enable();
          self.account = ethereum.selectedAddress
        } catch (error) {
          self.$alert('您拒绝了授权使用 MetaMask', '提示', {
            confirmButtonText: '确定',
          })
        }
      } else if (window.web3) {
        window.web3 = new Web3(web3.currentProvider)
        const account = web3.eth.accounts[0]
        if (!account){
          self.$alert('您的 MetaMask 被锁住了，请先解锁', '提示', {
            confirmButtonText: '确定',
          })
          return
        }
        self.account = account
      } else {
        self.$alert('您的浏览器还没有安装 MetaMask，请先安装该插件', '提示', {
          confirmButtonText: '确定',
        })
        return
      }
      store.state.account = self.account
    }
  },
  async created () {
    await this.unlockMetaMask()
    const contract = await this.initContract()
    const tokenContract = await this.initTokenContract()
    window.contract = contract
    window.tokenContract = tokenContract
    store.dispatch('getGameInfo', contract)
    store.dispatch('getWaitInfo', contract)
    store.dispatch('getBalance', this.account)
    // const data = {contract: contract, eventName: 'currentStepRaisedToken'}
    // store.dispatch('startListenEvent', data)
  },
  mounted () {
  }
}
</script>

<style lang='scss'>
@import "./assets/base.scss";
body {
  margin: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  background-image: url('~@/assets/image/bg.jpg');
  width: 100vw;
  height: 327vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  .banner {
    width: 100vw;
    height: 58vw;
    background-image: url('~@/assets/image/banner.jpg');
  }
  .desc {
    width: 88%;
    height: 12vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    span, a {
      display: flex;
      font-size: 4vw;
      font-weight: bold;
      color: $tc;
    }
    span:before {
      content: '';
      background-image: url('~@/assets/image/小人.jpg');
      background-size: cover;
      width: 5vw;
      height: 7vw;
      margin-right: 5px;
    }
  }
  .mask {
    position: absolute;
    height: 100%;
    width: 100vw;
    background-color: rgba(0, 0, 0, .6)
  }
  .rules {
    display: flex;
    background-image: url('~@/assets/image/背景5.png');
    width: 80vw;
    height: 126vw;
    position: absolute;
    z-index: 1000;
    top: 20vw;
    justify-content: center;
    align-items: center;
    .rules-container {
      width: 86%;
      height: 92%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    p {
      margin-top: 1vw;
      text-align: left;
      font-size: 12px;
    }
  }
}

.el-message-box {
  width: 80% !important;
}
</style>
