        </div>
        <div class="order" v-if="waitInfo.totalAmount > 0">
          <span>{{getDate(waitInfo.time)}}</span>
          <span>{{waitInfo.totalAmount}} ETH</span>
          <span>{{waitInfo.isWait ? '土地申请中' : '建楼收益中'}}</span>
          <span>{{waitInfo.isWait ? '' : getDate(parseInt(waitInfo.time && waitInfo.time.toString()) + 5 * 24 * 3600)}}</span>
        </div>
      </div>
    </div>
    <div class="account-info">
