</template>

<script>
import store from '../store'
import { mapState, mapGetters, mapActions } from 'vuex'
import Clipboard from 'clipboard'
import BigNumber from 'bignumber.js'

export default {
  name: 'home',
  components: {
  },
  data() {
    return {
      betAmount: 1,
      betItems: [
        {
          name: '建 平 楼',
          level: 'V1',
          amount: '1-5 ETH',
        },
        {
          name: '建 别 墅',
          level: 'V2',
          amount: '6-10 ETH',
        },
        {
          name: '建 城 堡',
          level: 'V3',
          amount: '11-15 ETH',
        }
      ],
      leftList: [
        { cn: '总  收  益', en: '', value: 0, index: 6 },
        { cn: '玩家资产', en: '', value: 0, index: 7 },
        { cn: '游戏次数', en: '', value: 0 },
        { cn: '邀请玩家', en: '', value: 0, index: 5 },
        { cn: '累计建楼收益', en: '', value: 0, index: 9 },
        { cn: '累计邀请收益 ', en: '', value: 0, index: 10 },
      ],
      rightList: [
        { cn: '玩家等级', en: '', value: 0, index: 2 },
        { cn: '推荐人', en: '', value: 0, index: 1 },
        { cn: '邀请码', en: '', value: 0, index: 0 },
      ],
      detailList: [
        { cn: '邀请码', value: 23, en: 'Invite the number' },
        { cn: '推荐人', value: 23, en: 'Invitation amount' },
        { cn: '分红等级', value: 23, en: 'Investment amount' },
        { cn: '投注总额', value: 23, en: 'Raise investment amount' },
        { cn: '节点等级', value: 23, en: 'Lucky prize' },
        { cn: '推荐好友个数', value: 23, en: 'Fission reward' },
      ],
      incomeList: [
        { cn: '总收益', en: '', value: 0 },
        { cn: '当前持仓', en: '', value: 0 },
        { cn: '可提现数量', en: '', value: 0 },
        { cn: '累计投注分红', en: '', value: 0 },
        { cn: '累计节点收益', en: '', value: 0 },
        { cn: '待提现收益', en: '', value: 0 },
      ]
    }
  },
  computed: {
    ...mapState({
      account: state => state.account,
      balance: state => state.balance,
      fixedBalance: state => state.fixedBalance,
      userInfo: state => state.userInfo,
      gameInfo: state => state.gameInfo,
      waitInfo: state => state.waitInfo,
      isWait: state => state.isWait,
      wait: state => state.wait,
    }),
    inviteUrl: state => `${window.location.origin}/#/?referrer=${state.userInfo && state.userInfo[0]}`
  },
  methods: {
    getDate(ts) {
      let date = new Date(ts * 1000)
      if (!ts) {
        date = new Date()
      }
      return `${date.getFullYear()}.${date.getMonth() + 1}.${date.getDate()}`
    },
    withdraw(i) {
      console.log(i)
      if (i === 2) {
        store.dispatch('redeem')
      } else if (i === 5) {
        store.dispatch('withdrawProfit')
      }
    },
    async getInviteCode() {
      const inviteCode = Math.random().toString().substr(2, 8)
      const result = await store.dispatch('isCode', inviteCode)
      console.log('isCode', inviteCode, result)
      if (result) {
        return inviteCode
      }
      return false
    },
    check() {
      if (this.betAmount % 1 === 0) {
        return true
      }
      return false
    },
    async bet() {
      if (!this.check()) {
        this.$alert('投注数量必须为整数')
        return
      }
      const urlReferrer =  this.$router.history.current.query.referrer
      console.log('url referrer ', urlReferrer)
      if (this.wait && this.isWait) {
        this.$alert('排队中...')
        return
      }
      let inviteCode = this.userInfo[0]
      while(!inviteCode) {
        inviteCode = await this.getInviteCode()
        console.log('in while loop', inviteCode)
      }
      const params = {
        inviteCode: inviteCode,
        referer: this.userInfo[1] || urlReferrer || '99999999',
        value: this.betAmount
      }
      store.dispatch('waitInvest', params)
    },
    selectBetAmount(i) {
      console.log(i)
      switch(i) {
        case 0: this.betAmount = 5; break;
        case 1: this.betAmount = 10; break;
        case 2: this.betAmount = 15; break;
        default: return;
      }
    },
    minus() {
      console.log('minus')
      if (this.betAmount <= 1) {
        console.log('can not minus')
        return
      }
      this.betAmount -= 1
    },
    add() {
      console.log('add')
      this.betAmount = parseInt(this.betAmount) + 1
    }
  },
  mounted() {
    const self = this
    const clipboard = new Clipboard('.copy-link')
    clipboard.on('success', function(e) {
      console.log('click invite url', self.userInfo[0])
      if (self.userInfo[0]) {
        self.$alert(`已经成功复制您的邀请地址: ${self.inviteUrl}`, '提示', {
          confirmButtonText: '确定',
        })
      } else {
        self.$alert('参与投注获取邀请码')
      }
      e.clearSelection()
    })
    clipboard.on('error', function(e) {
      console.log(e)
    })
  },
  created() {
    const account = '0x28e620f0bd68e8b8f3b50b1a61e54fd968e071b2'
    store.dispatch('getFixedBalance', account)
  }
}
</script>
